import React, {
  useState,
  useCallback
} from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import axios from 'axios';
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Checkbox,
  Divider,
  FormHelperText,
  Grid,
  TextField,
  Typography,
  Link,
  makeStyles
} from '@material-ui/core';
import FilesDropzone from 'src/components/FilesDropzone';

import ReCAPTCHA from "react-google-recaptcha";

const useStyles = makeStyles(() => ({
  root: {},
  editor: {
    '& .ql-editor': {
      height: 200
    }
  }
}));


const SchoolRegister = ({ className, ...rest }) => {
  const classes = useStyles();

  const [files, setFiles] = useState([]);

  const handleRemoveAll = () => {
    setFiles([]);
  };
  const handleDrop = useCallback((acceptedFiles) => {
    setFiles((prevFiles) => acceptedFiles);
  }, []);

  const onChange = (value)=> {
    console.log("Captcha value:", value);
  }
  const recaptchaRef = React.createRef();
    // const history = useHistory();
  // const { enqueueSnackbar } = useSnackbar();

  return (
    <Formik
      initialValues={{
        school_name: '',
        domain: '',
        images: [],
        address: '',
        name: '',
        phone: '',
        email: '',
        province: '',
        district: '',
        policy:false,
        submit: null

      }}
      validationSchema={Yup.object().shape({
        school_name: Yup.string().max(200).required('Bắt buộc nhập tên trường học'),
        domain: Yup.string().max(30).required('Bắt buộc chọn tên miền'),
        images: Yup.array(),
        name: Yup.string().max(55).required('Bắt buộc nhập tên'),
        phone: Yup.string().max(15).required('Bắt buộc nhập số điện thoại'),
        email: Yup.string().email('Cần nhập đúng thư điện tử').max(255).required('Bắt buộc nhập thư điện tử'),
        province: Yup.string().max(100).required('Bắt buộc nhập tỉnh/thành phố '),
        district: Yup.string().max(100).required('Bắt buộc nhập quận/huyện'),
        address: Yup.string().max(255).required('Bắt buộc nhập địa chỉ'),
        policy: Yup.boolean().oneOf([true], 'Bạn cần đồng ý với điều khoản')
      })}
      onSubmit={async (values, {
        setErrors,
        setStatus,
        setSubmitting
      }) => {
        try {
          const recaptchaValue = recaptchaRef.current.getValue();
          // NOTE: Make API request
          console.log(recaptchaValue);
          values.captcha=recaptchaValue;
          let re = await axios.get("https://dschool.vn:5000/school/register",values)
        console.log(re);
          setStatus({ success: true });
          setSubmitting(false);
          // enqueueSnackbar('Product Created', {
          //   variant: 'success'
          // });
          // history.push('/app/products');
        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        setFieldValue,
        touched,
        values
      }) => (
          <form
            onSubmit={handleSubmit}
            className={clsx(classes.root, className)}
            {...rest}
          >
            <Grid
              container
              spacing={3}
            >
              <Grid
                item
                xs={12}
                lg={8}
              >
                <Card>
                  <CardContent>

                    <Grid
                      container
                      spacing={3}
                    >
                      <Grid
                        item
                        md={6}
                        xs={12}
                      >
                        <TextField
                          error={Boolean(touched.school_name && errors.school_name)}
                          fullWidth
                          helperText={touched.school_name && errors.school_name}
                          label="Tên trường học"
                          name="school_name"
                          required
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values.school_name}
                          variant="outlined"
                        /></Grid>
                      <Grid
                        item
                        md={6}
                        xs={12}
                      >
                        <TextField
                          error={Boolean(touched.domain && errors.domain)}
                          fullWidth
                          helperText={touched.domain && errors.domain}
                          label="Tên miền"
                          name="domain"
                          required
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values.domain}
                          variant="outlined"
                        /></Grid>

                      <Grid
                        item
                        md={6}
                        xs={12}
                      >
                        <TextField
                          error={Boolean(touched.province && errors.province)}
                          fullWidth
                          helperText={touched.province && errors.province}
                          label="Tỉnh/thành phố "
                          name="province"
                          onBlur={handleBlur}
                          onChange={handleChange}
                          required
                          value={values.province}
                          variant="outlined"
                        />
                      </Grid>
                      <Grid
                        item
                        md={6}
                        xs={12}
                      >
                        <TextField
                          error={Boolean(touched.district && errors.district)}
                          fullWidth
                          helperText={touched.district && errors.district}
                          label="Quận/huyện "
                          name="district"
                          required
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values.district}
                          variant="outlined"
                        />
                      </Grid>
                      <Grid
                        item
                        md={12}
                        xs={12}
                      >
                        <TextField
                          error={Boolean(touched.address && errors.address)}
                          fullWidth
                          helperText={touched.address && errors.address}
                          label="Địa chỉ"
                          name="address"
                          required
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values.address}
                          variant="outlined"
                        /></Grid>
                    </Grid>
                  </CardContent>
                  <CardContent>
                    <Box
                      mt={3}
                      mb={1}
                    >
                      <Typography
                        variant="subtitle2"
                        color="textSecondary"
                      >
                        Logo trường
                    </Typography>
                    </Box>
                    <FilesDropzone handleDrop={handleDrop} files={files} handleRemoveAll={handleRemoveAll} />
                  </CardContent>
                </Card>


              </Grid>
              <Grid
                item
                xs={12}
                lg={4}
              >
                <Card>
                  <CardHeader title="Người đại diện" />
                  <Divider />

                  <CardContent>
                    <Box mt={2}>
                      <TextField
                        error={Boolean(touched.name && errors.name)}
                        fullWidth
                        helperText={touched.name && errors.name}
                        label="Họ và tên"
                        name="name"
                        required
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.name}
                        variant="outlined"
                      />
                    </Box>
                    <Box mt={2}>
                      <TextField
                        error={Boolean(touched.email && errors.email)}
                        fullWidth
                        helperText={touched.email && errors.email}
                        label="Thư điện tử"
                        name="email"
                        required
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.email}
                        variant="outlined"
                      />
                    </Box>
                    <Box mt={2}>
                      <TextField
                        error={Boolean(touched.phone && errors.phone)}
                        fullWidth
                        helperText={touched.phone && errors.phone}
                        label="Điện thoại"
                        name="phone"
                        required
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.phone}
                        variant="outlined"
                      />
                    </Box>
                  </CardContent>
                </Card>
                {errors.submit && (
                  <Box mt={3}>
                    <FormHelperText error>
                      {errors.submit}
                    </FormHelperText>
                  </Box>
                )}
                <Box
                  alignItems="center"
                  display="flex"
                  mt={2}
                  ml={-1}
                >
                  <Checkbox
                    checked={values.policy}
                    name="policy"
                    onChange={handleChange}
                  />
                  <Typography
                    variant="body2"
                    color="textSecondary"
                  >
                    Tôi đã đọc và đồng ý với
                        {' '}
                    <Link
                      component="a"
                      href="#"
                      color="secondary"
                    >
                      điều khoản và điều kiện
                        </Link>
                  </Typography>
                </Box>
                {Boolean(touched.policy && errors.policy) && (
                  <FormHelperText error>
                    {errors.policy}
                  </FormHelperText>
                )}
                {errors.submit && (
                  <Box mt={3}>
                    <FormHelperText error>
                      {errors.submit}
                    </FormHelperText>
                  </Box>
                )}
                <Box mt={2}>
                <ReCAPTCHA
                  sitekey="6LfI8cMZAAAAAGSKaeE7dDODE1WL-w_EurTVek-q"
                  onChange={onChange}
                  ref={recaptchaRef}
                />
                  <Button
                    color="secondary"
                    disabled={isSubmitting}
                    fullWidth
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    Đăng ký
                      </Button>
                </Box>
              </Grid>
            </Grid>
          </form>
        )
      }
    </Formik >
  );
};

SchoolRegister.propTypes = {
  className: PropTypes.string
};

export default SchoolRegister;